.groupOverLink {
background-color: #cccccccc;
xtransition:   0.6s ease-out ;

color: #333;
padding:10px;
height:40px;
border-radius: 20px;
width: 250px;
white-space: nowrap;
overflow: hidden;
    text-overflow: ellipsis;
}

.groupOverLink:hover {
    background-color: #cccccc;
    transition:   0.3s ease-in ;
    }
.groupOver {
  position: absolute;
  transition:   0.3s ease-in ;
 
  margin-left:20px;
  margin-right:20px;
  margin-top:10px;
  bottom:0px;
  z-index: 1;
    }