.channelTabs > ul {
  padding-left: 0px;
  padding-inline-start: 0px;
  padding-bottom: 6px;
  list-style-type: none;

  overflow: hidden;
}

.channelTabs li {
  padding-top: 4px;
  margin-right: 4px;

  padding-bottom: 3px;
  display: inline-block;
  border-bottom: 3px ;
}
.channelTabs li > a  {
 

  color: inherit;
  text-decoration: none;
}
.channelTabs li > span > a  {
 

  color: inherit;
  text-decoration: none;
}
.channelTabs li:hover {
  display: inline;
  border-bottom: 3px solid darkblue;
}

.channelTabs li:nth-child(1) {
  display: inline;
  border-bottom: 3px solid darkblue;
}

