.breakpoints{
margin-top: 12px;
}
.breakpoint {
padding:4px;
border-radius: 50%;
height:32px;
width: 32px;
color: #333333;
background-color: #cccccc;
line-height: 20px;
font-size: 16px;
text-align: center;
margin-right: 6px;
}
.breakpoint:hover {
    background-color: #333333;
color: #cccccc;
cursor: pointer;

    }


.breakpoint.selected {
    color: #cccccc;
background-color: #333;

    }
